<template>
  	<div class="login">

	  	<transition name="form-fade" mode="in-out">
	  		<section class="form_contianer" v-show="showLogin">
          <!-- <div class="login_logo"><img src="../../assets/image/ocms_title.png" alt=""></div> -->
		    	<h2>注册</h2>
            <a-form :layout="formLayout" @submit="submitRegist" :autoFormCreate="(form)=>{this.form = form}" :hideRequiredMark=true>
                <a-form-item
                  label="账户"
                  fieldDecoratorId="account"
                  :fieldDecoratorOptions="{rules: rules.account}"
                >

                  <a-input :placeholder="$t('login.user')" @focus="blueUsername()">
                    <!-- <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/> -->
                  </a-input>
              </a-form-item>
                <a-form-item
                  :label="this.$t('login.user')"
                  fieldDecoratorId="nickName"
                  :fieldDecoratorOptions="{rules: rules.nickName}"
                >

                  <a-input :placeholder="$t('login.user')" @focus="blueUsername()">
                    <!-- <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/> -->
                  </a-input>
                </a-form-item>
                <p class="loginErrorInfor" v-if="hasUserError">{{$t('error.userNotexist')}}</p>
                <a-form-item
                  :label="this.$t('login.password')"
                  fieldDecoratorId="password"
                   :fieldDecoratorOptions="{rules: rules.password}"
                >
                  <a-input type='password' :placeholder="$t('login.password')" @focus="bluePassword()">
                    <!-- <a-icon slot="prefix" type='lock' style="color:rgba(0,0,0,.25)"/> -->
                  </a-input>
                </a-form-item>
                <p class="loginErrorInfor" v-if="haspasswordError">{{$t('error.errorPassword')}}</p>
                <a-form-item
                  label="角色"
                  fieldDecoratorId="role"
                   :fieldDecoratorOptions="{rules: rules.password}"
                >
                    <a-select>
                      <a-select-option value="service">客服</a-select-option>
                      <a-select-option value="admin">管理员</a-select-option>
                    </a-select>
                </a-form-item>
                <a-form-item class="submitBtn">
                  <a-button
                    type='primary'
                   htmlType='submit'>
                    注册
                  </a-button>
                </a-form-item>
            </a-form>
            <!-- <p class="forget_lang">
                <lang class="switchLang" :showIcon=true></lang>
            </p> -->
            <a-button @click="gotoLogin">返回登录</a-button>

	  		</section>
	  	</transition>
  	</div>
</template>

<script>
// import lang from "@/components/change-lang";
// import { mapGetters } from "vuex";
import Cookies from "@/js/cookies";

export default {
  data() {
    return {
      formLayout: "vertical",
      loginForm: {
        username: "",
        password: ""
      },
      rules: {
        nickName: [
          {
            required: true,
            message: this.$t("login.errorUser"),
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            message: this.$t("login.errorPassword"),
            trigger: "blur"
          }
        ]
      },
      hasUserError: false,
      haspasswordError: false,
      showLogin: false
    };
  },
  components: {
    // lang
  },

  methods: {
    submitRegist(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log(values);
          this.$store.dispatch("register", values).then(
            res => {
              // this.$router.push({ name: "projects" });
            },
            error => {
              if (error.data != undefined) {
                if (error.data.code == 400001) {
                  //没有该用户
                  this.hasUserError = true;
                  this.errorString = this.$t("userManger.userNotExist");
                } else if (error.data.code == 400002) {
                  this.haspasswordError = true;
                  this.errorPasswordString = this.$t(
                    "userManger.loginErrorPassword"
                  );
                }
              }
            }
          );
        }
      });
    },
    blueUsername() {
      this.hasUserError = false;
    },
    bluePassword() {
      this.haspasswordError = false;
    },
    gotoLogin() {
      console.log(1111);
      this.$router.push("/login");
    }
  },
  mounted() {
    this.showLogin = true;
  }
};
</script>

<style lang="less">
@import "../../css/login.less";
</style>





